.login_body {
  color: #555;
  font-size: 15px;
  line-height: 1.7;
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', Arial, sans-serif;
  background: #f7fafc;
  -webkit-font-smoothing: subpixel-antialiased;
  padding-top: 0px !important;
}

.login_main {
  height: auto;
  padding-top: 6%;
}


.login_footer {
  font-size: 12px;
  line-height: 42px;
  text-align: center;
}

.login_footer span {
